import styled from 'styled-components';


export const PostContainer = styled.div`
${({ theme, size }) => css`
   width: 100%;
   margin: 0 auto;
   padding: 0 ${theme.spacings.large};
   ${postContainerStyles[size](theme)}
   @media ${theme.media.lteMedium} {
     padding: 0 calc(${theme.spacings.large} / 2);
   }
 `}
`;
